@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin: auto;
  margin: 0;
  font-family: Lato;
}

div {
  margin: auto;
  padding: 1em;
  float: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.userDiv {
    text-align: left;
    width: 250px;
    height: 100vh;
    padding: 1.5em;
    background-color: rgba(105, 0, 109, 0.7);
    color: white;
}

.userDiv p {
    font-size: 16px;
}

.userDiv h3 {
    font-size: 20px;
}
.feed {
    margin: 25px;
    padding: 25px;
    margin-top: 0;
    padding-top: 0;
    height: auto;
}

.feed button {
    background-color: rgba(105, 0, 109, 0.7);
    margin: 10px;
    border-radius: 10px;
    border: none;
    text-decoration: none;
    color: white;
    padding: 8px 12px;
    font-size: 1.2em;
    display: block;
}

.feed p {
    text-align: left;
    padding: 25px;
    border-bottom: solid black 2px;
}

.feed img {
    width: 800px;
}
.comments {
    margin: 25px;
    padding: 25px;
    margin-top: 0;
    padding-top: 0;
    
}

.textBox {
    width: 600px;
    height: 30px;
}

.enter {
    background-color: rgba(105, 0, 109, 0.7);
    margin: 10px;
    border-radius: 10px;
    border: none;
    text-decoration: none;
    color: white;
    padding: 8px 12px;
    font-size: 1.2em;
}

.section p {
    margin: 0;
    padding-left: 10px;
    margin-bottom: 15px;
    text-align: left;
    border-left: solid grey 2px;
    width: 600px;
}
